.header-main {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  //background-color: #2768b4
  &__body {
    display: flex;
    align-items: center;
    height: 50px;
    @include breakpoint(sm) {
      height: 80px; } }

  &__logo {
    margin: 0 35px 0 0;
    @include breakpoint(sm) {
      margin: 0 70px 0 0; }
    @include breakpoint(lg) {
      margin: 0 40px 0 0; }
    @include breakpoint(xl) {
      margin: 0 90px 0 0; }
    .logo-full {
      display: none;
      visibility: hidden;
      @include breakpoint(420) {
        display: inline-block;
        visibility: visible; } }
    .logo-min {
      @include breakpoint(420) {
        display: none;
        visibility: hidden; } }
    img {
      max-height: 24px;
      @include breakpoint(sm) {
        max-height: 35px; } } }

  &__contacts {
    display: flex;
    align-items: center;
    margin: 0 auto 0 0;
    @include breakpoint(lg) {
      margin: 0 40px 0 0; }
    svg {
      fill: #ffffff;
      margin: 0 10px 0 0;
      @include breakpoint(sm) {
        margin: 0 15px 0 0; } } }

  &__phones {
    display: flex;
    flex-direction: column;
    a {
      font-size: 12px;
      white-space: nowrap;
      &:hover {
        color: #7EB5F9; }
      @include breakpoint(sm) {
        font-size: 14px; } } }

  &__cart {
    position: relative;
    margin: 0 35px 0 0;
    @include breakpoint(lg) {
      margin: 0; }
    svg {
      fill: #ffffff; }
    span {
      position: absolute;
      font-size: 7px;
      color: #ffffff;
      top: -3px;
      display: flex;
      align-items: center;
      justify-content: center;
      right: -10px;
      width: 15px;
      height: 15px;
      background-color: #D11586;
      border-radius: 50%;
      line-height: 15px; } }
  a {
    transition: color .1s ease-out; }

  .burger-menu {
    justify-self: end;
    z-index: 7; } }
