.parallax-main {
  padding: 50px 0 55px 0;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  overflow: hidden;

  @include breakpoint(sm) {
    padding: 80px 0 25px 0; }
  @include breakpoint(lg) {
    min-height: 100vh;
    padding: 80px 0 55px 0; }

  &__container {}


  &__layer {
    position: absolute;
    top: -2%;
    right: 0;
    bottom: 0;
    left: -2%;
    transition: transform .1s ease-out; }
  &__bg {
    background: linear-gradient(180deg, #4C79C3 0%, #7FA5E0 46.52%, #A3BEF4 100%); }
  &__layer-1 {
    background: url("../img/main-parallax/layer-1.png") center top no-repeat;
    background-size: cover!important; }
  &__layer-2 {
    background: url("../img/main-parallax/layer-2.png") center top no-repeat;
    background-size: cover!important; }
  &__layer-3 {
    background: url("../img/main-parallax/layer-3.png") center top no-repeat;
    background-size: cover!important; }
  &__layer-4 {
    background: url("../img/main-parallax/layer-4.png") center top no-repeat;
    background-size: cover!important; }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
    padding: 40px 0;
    position: relative;
    margin: 0 0 55px 0;
    @include breakpoint(lg) {
      //margin: 40px 0 55px 0
      margin: 0; }
    &:before {
      content: '';
      position: absolute;
      width: 300%;
      left: -100%;
      top: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.1);
      @include breakpoint(lg) {
        width: 130%;
        left: -15%;
        transform: skew(-.312rad); } } }


  &__desc {
    font-size: 18px;
    line-height: 30px;
    color: #ffffff;
    display: block;
    margin: 0 0 20px 0;
    z-index: 1; }


  &__btn {
    display: flex;
    justify-content: flex-start;
    z-index: 1; }


  &__body {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    z-index: 1;
    justify-content: center; }



  &__title {
    font-size: 35px;
    line-height: 40px;
    padding: 0 0 0 25px;
    margin: 0 0 20px 0;
    &:before {
      width: 7px;
      background-color: #D11586; }
    @include breakpoint(sm) {
      font-size: 38px;
      line-height: 45px; } }

  &__arrow {
    @include breakpoint(lg) {
      position: absolute;
      bottom: 15px;
      left: 50%;
      width: 15px;
      height: 15px;
      border-left: 2px solid rgba(255, 255, 255, 0.6);
      border-bottom: 2px solid rgba(255, 255, 255, 0.6);
      &_1 {
        animation: arrow-animation-1 infinite 2s; }
      &_2 {
        border-left: 2px solid rgba(255, 255, 255, 0.4);
        border-bottom: 2px solid rgba(255, 255, 255, 0.4);
        bottom: 17px;
        animation: arrow-animation-2 infinite 2s; } } } }








@keyframes arrow-animation-1 {
  from {
    transform: translateX(-50%) rotate(-45deg) translateY(-20px);
    opacity: 0; }
  to {
    opacity: 1;
    transform: translateX(-20px) rotate(-45deg) translateY(0); } }

@keyframes arrow-animation-2 {
  0% {
    transform: translateX(-50%) rotate(-45deg) translateY(-20px);
    bottom: 27px;
    opacity: 0; }
  50% {
    opacity: 0;
    bottom: 25px; }
  100% {
    opacity: 1;
    bottom: 23px;
    transform: translateX(-20px) rotate(-45deg) translateY(0); } }
