$font-family: 'Roboto';
$font-size: 18px;
$container-width: 1200px;  //1200
$gutter: 30px;

// difference with padding on mobile and desktop
$diff-padd: 5px;

//Cassidy
$v-step-large: 100px;
$v-step-small: 50px;
// breakpoints================================================
$screen-min-min: 320px;
// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;  //540
// Small tablets (portrait view)
$screen-md-min: 768px;  //720
// Tablets and small desktops
$screen-lg-min: 992px; // 960
// Large tablets and desktops
$screen-xl-min: 1200px; //1140
// Dimensions resolutions for building columns styles
$dims: min sm md lg xl;
// ===========================================================

