//My custom burger menu style style========================================
.burger-menu {
  position: relative;
  display: flex;
  width: 22px;
  height: 18px;
  @include breakpoint(sm) {
    width: 32px;
    height: 25px; }
  &:before, &:after {
    content: '';
    position: absolute;
    background-color: #FFFFFF;
    height: 2px;
    width: 16px;
    left: calc(50% - 8px);
    transition: all 0.3s ease-out;
    @include breakpoint(sm) {
      width: 26px;
      left: calc(50% - 13px); } }
  &:before {
    top: 0; }
  &:after {
    bottom: 0; }
  span {
    position: absolute;
    top: calc(50% - 1px);
    height: 2px;
    width: 100%;
    background-color: #ffffff;
    transition: transform 0.1s ease-out; }
  &.active {
    &:before {
      transform: rotate(45deg);
      top: 50%;
      width: 100%;
      left: 0; }
    &:after {
      transform: rotate(-45deg);
      top: 50%;
      width: 100%;
      left: 0; }
    span {
      transform: scale(0); } }
  @include breakpoint(lg) {
    display: none; } }


.menu-main {
  position: fixed;
  right: -100%;
  width: calc(100% - 50px);
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  transition: right 0.3s ease-out;
  padding: 70px 20px 20px 20px;
  z-index: 5;
  overflow: auto;
  box-shadow: none;
  @include breakpoint(sm) {
    width: calc(100% - 100px); }
  @include breakpoint(lg) {
    position: static;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    background: none;
    backdrop-filter: none;
    padding: 0;
    margin: 0 15px 0 0;
    overflow: visible; }
  >ul {
    @include breakpoint(lg) {
      display: flex; }
    .has-submenu {
      ul {
        overflow: hidden;
        //max-height generating via JavaScript
        transition: max-height 0.3s ease-out;
        padding: 0 0 0 10px;
        @include breakpoint(lg) {
          background-color: rgba(255, 255, 255, 0.8);
          position: absolute;
          display: block;
          padding: 0 30px; }
        a {
          font-size: 14px;
          padding: 10px 0;
          @include breakpoint(lg) {
            color: #0066BA;
            font-weight: 500;
            &:hover {
              color: #7EB5F9; } } }
        > li {
          @include breakpoint(lg) {
            &:first-child {
              padding: 20px 0 0 0; }
            &:last-child {
              padding: 0 0 20px 0; } }

          //&:first-child
          //  a
          //    +breakpoint(lg)
          //      margin-top: 15px
          //&:last-child
          //  a
          //    +breakpoint(lg)
 } }          //      margin-bottom: 15px
      >a {
        user-select: none;
        display: flex;
        align-items: center;
        &:after {
          content: '';
          display: inline-block;
          width: 0;
          height: 0;
          border-top: 8px solid #ffffff;
          border-right: 6px solid transparent;
          border-left: 6px solid transparent;
          margin: 0 0 0 7px;
          transition: transform .3s ease-out;
          transform: rotate(0); }
        &:hover {
          color: #ffffff; } }
      &.closed {
        ul {
          margin: 0; }
        >a {
          &:after {
            transform: rotate(90deg); } } } }
    >li {
      @include breakpoint(lg) {
        margin: 0 25px 0 0; }
      >a {
        &:hover {
          color: #7EB5F9; } } } }



  a {
    display: inline-block;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: #ffffff;
    text-transform: uppercase;
    padding: 15px 0;
    &.active {
      color: #7EB5F9; } }


  &.active {
    right: 0; } }

