// HEADING STYLES
.title {
  display: block;
  color: #ffffff;
  font-weight: 700;
  font-size: 28px;
  line-height: 30px;
  position: relative;
  padding: 0 0 0 20px;
  &:before {
    content: '';
    position: absolute;
    width: 5px;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #ffffff; } }


//End heading style

// BTN STYLES
.btn-main {
  display: inline-block;
  position: relative;
  color: #ffffff;
  font-size: 14px;
  line-height: 25px;
  font-weight: 900;
  letter-spacing: .16em;
  text-transform: uppercase;
  background-color: #D11586;
  padding: 15px 25px;
  @include breakpoint(lg) {
    background: none;
    &:after,
    &:before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      width: 65%;
      z-index: -1;
      transition: all .2s ease-out;
      pointer-events: none; }

    &:after {
      left: -5%;
      background-color: #D11586;
      transform: translate(0px,-20%) skew(-.312rad); }

    &:before {
      right: -5%;
      background-color: #7EB5F9;
      transform: translate(0px,20%) skew(-.312rad);
      opacity: 1; }

    &:hover {
      &:after {
        left: 0;
        width: 100%;
        transform: translate(0px,0px) skew(0); }
      &:before {
        right: 0;
        width: 100%;
        transform: translate(0px,0px) skew(0);
        opacity: 0; } } } }

// End BTN STYLES
